.header {
  width: 100%;
  height: 5rem;
  display: flex;
  padding: 0 10%;
  justify-content: space-between;
  align-items: center;
  /* background-color: #008080; */
  background-color: blue;
}

.logo {
  font-size: 2rem;
  /* color: white; */
  /* color: black; */
}

img {
  width: 60px;
  height: auto;
  background-color: white;
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-left: 1.5rem;
  font-size: 1.25rem;
}

.nav a {
  text-decoration: none;
  /* color: #88dfdf; */
  color: whitesmoke;
}

.nav a:hover,
.nav a:active,
.nav a.active {
  /* color: #e6fcfc; */
  color: white;
  border: 1px solid white;
  padding: 5px;
  border-radius: 5px;
  
}
